	import {initPicker,initDays} from "@/common/util.js";
	import provinces from './city-data/province.js';
	import citys from './city-data/city.js';
	import areas from './city-data/area.js';
	export default {
		data() {
			return {
				result:[],
				data:{},
				checkArr:[],
				checkValue:[],
				pickVal:[],
				showPicker:false,
				resultStr:"",
				itemHeight:`height: ${uni.upx2px(88)}px;`
			};
		},
		computed:{
			
		},
		props:{
			mode:{
				type:String,
				default(){
					return "date"
				}
			},
			themeColor:{
				type:String,
				default(){
					return "#f00"
				}
			},
			startYear:{
				type:String,
				default(){
					return "1970"
				}
			},
			endYear:{
				type:String,
				default(){
					return new Date().getFullYear()+''
				}
			},
			defaultVal:{
				type:Array,
				default(){
					return [0,0,0,0,0,0]
				}
			},
			step:{
				type:null,
				default:1
			},
			current:{
				type:Boolean,
				default:false
			},
			selectList:{
				type:Array,
				default(){
					return [];
				}
			}
		},
		watch:{
			mode(){
				this.initData();
			},
			selectList(newd,oldd){
                console.log(newd)
				console.log(oldd)
				this.data = newd
        }
			
		},
		methods:{
			useCurrent(){
				let aToday=new Date();
				let tYear=aToday.getFullYear().toString();
				let tMonth=this.formatNum(aToday.getMonth()+1).toString();
				let tDay=this.formatNum(aToday.getDate()).toString();
				let tHours=this.formatNum(aToday.getHours()).toString();
				let tMinutes=this.formatNum(aToday.getMinutes()).toString();
				let tSeconds=this.formatNum(aToday.getSeconds()).toString();
				if(this.current){
					return [tYear,tMonth,tDay,tHours,tMinutes,tSeconds];
				}else{
					return this.defaultVal;
				}
			},
			formatNum(num){
				return num<10?'0'+num:num+'';
			},
			maskTap(){
				this.showPicker = false;
			},
			show(){
				this.showPicker = true;
			},
			hide(){
				this.showPicker = false;
			},
			pickerCancel(){
				this.$emit("cancel",{
					checkArr:this.checkArr,
					defaultVal:this.pickVal
				});
				this.showPicker = false;
			},
			pickerConfirm(e){
				console.log(e)
				console.log(this)
				this.$emit("confirm",{
					checkArr:this.checkArr?this.checkArr:this.data[0],
					defaultVal:this.pickVal,
					result:this.resultStr?this.resultStr:this.data[0].label
				});
				this.showPicker = false;
			},
			bindChange(val){
				let _this=this;
				let arr=val.detail.value;
				let checkArr=_this.checkArr;
				let mode=_this.mode;
				switch(mode){
					case "selector":
						_this.checkArr=_this.data[arr[0]];
						_this.resultStr=_this.data[arr[0]].label;
						break;	
				}
				_this.$nextTick(()=>{
					_this.pickVal=arr;
				})
			},
			initData(){
				let _this=this;
				let data={};
				let mode=_this.mode;
				let year,month,day,hour,minute,second,province,city,area;
				 if(mode=="selector"){
					data=_this.selectList;
					console.log(_this.selectList)
				}else{
					data=initPicker(_this.startYear,_this.endYear,_this.mode,_this.step,_this.useCurrent(),_this.current);
				};
				_this.data=data;
				let dVal=(data.defaultVal&&_this.current)?data.defaultVal:_this.defaultVal;
				switch(mode){
					case "selector":
						_this.checkArr=data[dVal[0]];
						_this.resultStr=data[dVal[0]].label;
						break;
				}
				_this.$nextTick(()=>{
					if(data.defaultVal&&_this.current){
						_this.pickVal=data.defaultVal;
					}else{
						_this.pickVal=_this.defaultVal;
					}
				})
			}
		},
		mounted() {
			this.initData();
		}
	}
